import React from 'react';
import {pageLoading} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentDeterminationPeople from '../../components/determination-people';

class PageDeterminationPeople extends React.Component {
  componentDidMount() {
    const {pageLoading} = this.props;
    pageLoading(false);
  }
  
  componentDidUpdate() {
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="determination-people">
          <ComponentDeterminationPeople />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageDeterminationPeople);