import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import dataSettigns from '../../../config/data-settings';

class ComponentNews extends React.Component {
  componentDidMount() {
  }
  
  componentDidUpdate() {
  }
  
  componentWillUnmount() {
  }

  render() {
    const {sys, news} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.news} ${style[componentTheme]}`}>
        <div className={style.items}>
          {news.map((item, itemIndex) => (
          <Link key={itemIndex} className={`${style.item}`} to={`/external/${encodeURIComponent(item.link)}`}>
            <div className={`${style.image}`}>
              <img src={`${dataSettigns.siteUrl}${item.image}`} alt="" />
            </div>
            <div className={`${style.title}`}>
              {item.title}
            </div>
            <div className={`${style.date}`}>
              {item.date}
            </div>
          </Link>
          ))}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  news: state.news
});

export default connect(
  mapStateToProps
)(ComponentNews);