import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import dataSettigns from '../../../config/data-settings';

class ComponentSlider extends React.Component {
  sliderInterval;

  componentConstructor() {
  }

  componentRefresh() {
    if(this.sliderInterval) {
      clearInterval(this.sliderInterval);
    }
    const {slider} = this.props;
    if(slider.slides.length) {
      document.querySelector(`.${style.slide}:nth-child(1)`).classList.add(`${style['status--active']}`);
      let slides = document.querySelectorAll(`.${style.slide}`);
      this.sliderInterval = setInterval(() => {
        let currentSlide = document.querySelector(`.${style.slide}.${style['status--active']}`);
        let activeSlide = [...slides].indexOf(currentSlide) + 1;
        activeSlide = ((activeSlide === slides.length) ? 0 : activeSlide);
        currentSlide.classList.remove(`${style['status--active']}`);
        document.querySelector(`.${style.slide}:nth-child(${activeSlide+1})`).classList.add(`${style['status--active']}`);
      }, 4000);
    }
  }

  componentDestructors() {
    if(this.sliderInterval) {
      clearInterval(this.sliderInterval);
    }
  }

  componentDidMount() {
    this.componentConstructor();
    this.componentRefresh();
  }
  
  componentDidUpdate() {
    this.componentRefresh();
  }
  
  componentWillUnmount() {
    this.componentDestructors();
  }

  render() {
    const {sys, slider} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.slider} ${style[componentTheme]}`}>
        <div className={`${style.slides}`}>
          {slider.slides.map((slide, slideIndex) => (
          <Link key={slideIndex} className={`${style.slide}`} to={`/external/${encodeURIComponent(slide.link)}`}>
            <div className={`${style.image}`}>
              <img src={`${dataSettigns.siteUrl}${slide.image}`} alt="" />
            </div>
          </Link>
          ))}
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  slider: state.slider
});

export default connect(
  mapStateToProps
)(ComponentSlider);