import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';
import dataSettigns from '../../../config/data-settings';

class ComponentLogin extends React.Component {
  render() {
    const {sys, size} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    let componentSize = '';
    switch(size) {
      case 'small':
        componentSize = 'size--small';
        break;
      case 'large':
        componentSize = 'size--large';
        break;
      default:
        componentSize = '';
    }
    let string = generateLanguageString(languages, sys.language.name);
    let avatar = '';
    if(sys.theme.blindness) {
      avatar = `${process.env.PUBLIC_URL}/files/images/avatar--blindness.jpg`;
    } else {
      switch(sys.theme.name) {
        case 'gold':
          avatar = `${process.env.PUBLIC_URL}/files/images/avatar--gold.jpg`;
          break;
        default:
          avatar = `${process.env.PUBLIC_URL}/files/images/avatar--green.jpg`;
      }
    }
    return (
      <div className={`${style.login} ${componentSize && style[componentSize]} ${style[componentTheme]}`}>
        <div className={`${style.image}`}>
          <img src={avatar} alt="" />
        </div>
        <Link className={`${style.button}`} to={`/external/${encodeURIComponent(dataSettigns.login(sys.language.name))}`}>
          {string.general.login}
        </Link>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentLogin);