import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';

class ComponentEServices extends React.Component {
  servicesElement;

  activeCategory(index) {
    this.servicesElement.querySelectorAll(`.${style.category}`).forEach(category => {
      category.classList.remove(`${style['status--active']}`);
    });
    this.servicesElement.querySelectorAll(`.${style.list}`).forEach(list => {
      list.classList.remove(`${style['status--active']}`);
    });
    this.servicesElement.querySelector(`.${style.category}:nth-child(${(index + 1)})`).classList.add(`${style['status--active']}`);
    this.servicesElement.querySelector(`.${style.list}:nth-child(${(index + 1)})`).classList.add(`${style['status--active']}`);
  }

  handleCategoryClick = category => {
    this.activeCategory([...this.servicesElement.querySelectorAll(`.${style.category}`)].indexOf(category));
  }

  componentConstructor() {
    this.servicesElement = document.querySelector(`.${style.services}`);
  }

  componentRefresh() {
    const {eServices} = this.props;
    this.servicesElement.querySelectorAll(`.${style.category}`).forEach(category => {
      category.removeEventListener('click', () => this.handleCategoryClick(category));
    });
    this.servicesElement.querySelectorAll(`.${style.category}`).forEach(category => {
      category.addEventListener('click', () => this.handleCategoryClick(category));
    });
    if(eServices.categories.length) {
      this.activeCategory(0);
    }
  }

  componentDestructors() {
  }

  componentDidMount() {
    this.componentConstructor();
    this.componentRefresh();
  }
  
  componentDidUpdate() {
    this.componentRefresh();
  }
  
  componentWillUnmount() {
    this.componentDestructors();
  }

  getListSize(list) {
    const {size} = this.props;
    return (size === 'brief' ? list.slice(0, 4) : list);
  }

  render() {
    const {theme, eServices} = this.props;
    let componentTheme = generateComponentTheme(theme);
    return (
      <div className={`${style.services} ${style[componentTheme]}`}>
        <div className={`${style.header}`}>
          <div className={`${style.categories}`}>
            {eServices.categories.map((category, categoryIndex) => (
            <div key={categoryIndex} className={`${style.category}`}>
              {category.name}
            </div>
            ))}
          </div>
        </div>
        <div className={`${style.main}`}>
          <div className={`${style.lists}`}>
            {eServices.categories.map((category, categoryIndex) => (
            <div key={categoryIndex} className={`${style.list}`}>
              <div className={`${style.items}`}>
                {this.getListSize(category.list).map((item, itemIndex) => (
                <Link key={itemIndex} className={`${style.item}`} to={`/external/${encodeURIComponent(item.link)}`}>
                  <div className={`${style['e-service']}`}>
                    <div className={`${style['icon-container']}`}>
                      <div className={`${style.icon}`} style={{WebkitMaskImage: `url('${process.env.PUBLIC_URL}/files/images/service.png')`}}>
                      </div>
                    </div>
                    <div className={`${style['name-container']}`}>
                      <div className={`${style.name}`}>
                        {item.name}
                      </div>
                    </div>
                  </div>
                </Link>
                ))}
              </div>
            </div>
            ))}
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  theme: state.sys.theme,
  eServices: state.eServices
});

export default connect(
  mapStateToProps
)(ComponentEServices);