import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';
import ComponentIcon from '../icon';

class ComponentPopup extends React.Component {
  render() {
    const {sys, activeStatus, activeController} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    let componentActive = activeStatus ? 'status--active' : '';
    return (
      <div className={`${style.popup} ${style[componentDirection]} ${style[componentTheme]} ${componentActive && style[componentActive]}`}>
        <div className={style.box}>
          <div className={style.button} onClick={ev => {activeController(false)}}>
            <ComponentIcon name="close" size="small" />
          </div>
          <div className={style.container}>
            {this.props.children}
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentPopup);