import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import logo from './logo.png';
import nameAr from './name--ar.png';
import nameEn from './name--en.png';
import building from './building.png';

class ComponentIntro extends React.Component {
  state = {
    animating: false,
    loaded: false 
  };

  componentConstructor() {
    Promise.all([...document.querySelectorAll(`.${style.intro} img`)].map(image => ({
      element: image,
      complete: image.complete
    })).filter(image => !image.complete).map(image => {
      return new Promise(function(resolve, reject) {
        image.element.addEventListener('load', resolve);
      });
    })).then(values => {
      this.setState({
        animating: true
      });
    });
    const items = [];
    document.querySelector(`.${style.intro}`).addEventListener('animationstart', ev => {
      items.push({element: ev.target, complete: false});
    });
    document.querySelector(`.${style.intro}`).addEventListener('animationend', ev => {
      items[items.findIndex(item => item.element === ev.target)].complete = true;
      if(!items.filter(item => !item.complete).length) {
        this.setState({
          loaded: true
        });
      }
    });
  }

  componentRefresh() {
  }

  componentDestructors() {
  }

  componentDidMount() {
    this.componentConstructor();
    this.componentRefresh();
  }
  
  componentDidUpdate() {
    this.componentRefresh();
  }
  
  componentWillUnmount() {
    this.componentDestructors();
  }

  render() {
    const {sys} = this.props;
    let componentActive = '';
    if(!this.props.app.loaded || !this.state.loaded) {
      componentActive = 'status--active';
    }
    let animating = '';
    if(this.state.animating) {
      animating = 'status--animating';
    }
    let name = '';
    switch(sys.language.name) {
      case 'en':
        name = nameEn;
        break;
      default:
        name = nameAr;
    }
    return (
      <div className={`${style.intro} ${style[componentActive]} ${style[animating]}`}>
        <div className={style.logo}>
          <img src={logo} alt="" />
        </div>
        <div className={style.name}>
          <img src={name} alt="" />
        </div>
        <div className={style.building}>
          <img src={building} alt="" />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  app: state.app
});

export default connect(
  mapStateToProps
)(ComponentIntro);