import React from 'react';
import {changeSysTheme} from '../../../state/actions';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';

class ComponentSettings extends React.Component {
  changeTheme = (name) => {
    const {sys, changeSysTheme} = this.props;
    changeSysTheme(name, sys.theme.blindness);
  };

  render() {
    const {sys} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    let string = generateLanguageString(languages, sys.language.name);
    return (
      <div className={`${style.settings} ${style[componentDirection]} ${style[componentTheme]}`}>
        <div className={style.items}>
          <div className={style.item}>
            <div className={style.label}>
              {string.settings.label}
            </div>
            <div className={`${style.options}`}>
              <div className={`${style.option} ${style['type--green']} ${sys.theme.name === 'green' && style['status--active']}`} onClick={ev => {this.changeTheme('green')}}></div>
              <div className={`${style.option} ${style['type--gold']} ${sys.theme.name === 'gold' && style['status--active']}`} onClick={ev => {this.changeTheme('gold')}}></div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

const mapDispatchToProps = dispatch => ({
  changeSysTheme: (name, blindness) => {
    dispatch(changeSysTheme(name, blindness));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ComponentSettings);