import React from 'react';
import {sideMenusStartActive, sideMenusEndActive, popupSatisfactionActive, popupLanguageActive} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';
import PartHeader from '../../parts/header';
import PartFooter from '../../parts/footer';
import PartSideMenu from '../../parts/side-menu';
import ComponentPopup from '../../components/popup';
import ComponentSatisfaction from '../../components/satisfaction';
import ComponentLangauges from '../../components/langauges';

class LayoutBasic extends React.Component {
  render() {
    const {sys, page, startActive, endActive, satisfactionActive, languageActive, sideMenusStartActive, sideMenusEndActive, popupSatisfactionActive, popupLanguageActive, pageName} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.layout} ${style[componentDirection]} ${style[componentTheme]} ${startActive && style['theme--collapsed--start']} ${endActive && style['theme--collapsed--end']}`}>
        <div className={style.header}>
          <PartHeader />
        </div>
        <div className={style.main}>
          {page.loading && (
            <div className={style.loading}></div>
          )}
          <div className={`${style.components} ${style[`style--${pageName}`]}`}>
            {this.props.children.length ? this.props.children.map((child, childIndex) => (
            <div key={childIndex} className={style.component}>
              {child}
            </div>
            )) : this.props.children}
          </div>
        </div>
        <div className={style.footer}>
          <PartFooter />
        </div>
        <div className={`${style['side-menu']} ${style['type--start']} ${startActive && style['status--active']}`}>
          <PartSideMenu type="start" activeStatus={startActive} activeController={sideMenusStartActive} />
        </div>
        <div className={`${style['side-menu']} ${style['type--end']} ${endActive && style['status--active']}`}>
          <PartSideMenu type="end" activeStatus={endActive} activeController={sideMenusEndActive} />
        </div>
        <ComponentPopup activeStatus={satisfactionActive} activeController={popupSatisfactionActive}>
          <ComponentSatisfaction />
        </ComponentPopup>
        <ComponentPopup activeStatus={languageActive} activeController={popupLanguageActive}>
          <ComponentLangauges />
        </ComponentPopup>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  page: state.page,
  startActive: state.sideMenus.start,
  endActive: state.sideMenus.end,
  satisfactionActive: state.popups.satisfaction,
  languageActive: state.popups.languages
});

const mapDispatchToProps = dispatch => ({
  sideMenusStartActive: (active) => {
    dispatch(sideMenusStartActive(active));
  },
  sideMenusEndActive: (active) => {
    dispatch(sideMenusEndActive(active));
  },
  popupLanguageActive: (active) => {
    dispatch(popupLanguageActive(active));
  },
  popupSatisfactionActive: (active) => {
    dispatch(popupSatisfactionActive(active));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(LayoutBasic);