import React from 'react';
import {popupSatisfactionActive, popupLanguageActive} from '../../../state/actions';
import {connect} from 'react-redux';
import style from './style.module.css';
import PartLogo from '../logo';
import ComponentIcon from '../../components/icon';

class PartHeader extends React.Component {
  render() {
    const {satisfactionActive, languageActive, popupSatisfactionActive, popupLanguageActive} = this.props;
    return (
      <div className={style.header}>
        <div onClick={ev => {popupSatisfactionActive(!satisfactionActive)}}>
          <ComponentIcon name="satisfaction" />
        </div>
        <PartLogo />
        <div onClick={ev => {popupLanguageActive(!languageActive)}}>
          <ComponentIcon name="languages" />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  satisfactionActive: state.popups.satisfaction,
  languageActive: state.popups.languages
});

const mapDispatchToProps = dispatch => ({
  popupSatisfactionActive: (active) => {
    dispatch(popupSatisfactionActive(active));
  },
  popupLanguageActive: (active) => {
    dispatch(popupLanguageActive(active));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PartHeader);