import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';

class ComponentSearch extends React.Component {
  render() {
    const {sys} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.search} ${style[componentTheme]}`}>
        <form action="#">
          <input type="text" />
          <input type="submit" value="" />
        </form>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentSearch);