import {
  SIDE_MENUS_START_ACTIVE,
  SIDE_MENUS_END_ACTIVE
} from '../actions/types';

const initialState = {
  start: false,
  end: false
};

const sideMenus = (state = initialState, action) => {
  switch (action.type) {
    case SIDE_MENUS_START_ACTIVE:
      return {
        ...state,
        start: action.payload.active,
        end: action.payload.active ? false : state.end.active
      };
    case SIDE_MENUS_END_ACTIVE:
      return {
        ...state,
        start: action.payload.active ? false : state.start.active,
        end: action.payload.active
      };
    default:
      return state;
  }
};

export default sideMenus;