import React from 'react';
import {pageLoading, aboutUsLoaded} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import dataSettigns from '../../../config/data-settings';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentBlocks from '../../components/blocks';
import ComponentHTML from '../../components/html';
import aboutUsEn from '../../../cache/749da51d0c1b3d56982e9511a7e01d1c.cache';
import aboutUsAr from '../../../cache/0b39b8fa4777e4b909633e0dcabdf4fc.cache';

class PageAboutUs extends React.Component {
  language = {};

  getData = () => {
    const {sys, aboutUs, pageLoading, aboutUsLoaded} = this.props;
    this.language = sys.language;
    const promises = [];
    if(!(aboutUs.sections.length && Object.keys(aboutUs.map).length)) {
      promises[promises.length] = new Promise(function(resolve, reject) {
        fetch(dataSettigns.aboutUs(sys.language.name))
        .then(data => data.json())
        .then(data => {
          resolve({action: aboutUsLoaded, data});
        })
        .catch(reject);
      });
    }
    if (promises) {
      Promise.all(promises)
      .then(values => {
        values.forEach(value => {
          value.action(value.data);
        });
        pageLoading(false);
      })
      .catch(reason => {
        Promise.all([
          new Promise(function(resolve, reject) {
            fetch(sys.language.name === 'en' ? aboutUsEn : aboutUsAr)
            .then(data => data.json())
            .then(data => {
              resolve({action: aboutUsLoaded, data});
            })
          })
        ])
        .then(values => {
          values.forEach(value => {
            value.action(value.data);
          });
          pageLoading(false);
        });
      });
    } else {
      pageLoading(false);
    }
  };

  componentDidMount() {
    this.getData();
  }
  
  componentDidUpdate() {
    const {sys, pageLoading} = this.props;
    if(this.language.name !== sys.language.name) {
      pageLoading(true);
      this.getData();
    }
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys, aboutUs} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    const blocks = aboutUs ? aboutUs.sections.map((section, sectionIndex) => {
      return {
        name: section.name,
        icon: `about-us--block--${(sectionIndex + 1)}.png`,
        content: {
          type: 'components',
          components: section.content.map((item, itemIndex) => <ComponentHTML key={itemIndex} html={item} />)
        }
      };
    }) : [];
    let string = generateLanguageString(languages, sys.language.name);
    blocks[blocks.length] = {
      name: string.aboutUs.map,
      icon: 'about-us--block--map.png',
      link: `/external/${encodeURIComponent(`https://www.google.com/maps/embed?pb=!1m10!1m8!1m3!1d13709.625469972087!2d${aboutUs.map.longitude}!3d${aboutUs.map.latitude}!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2seg!4v1571626217940!5m2!1sen!2seg`)}`,
      content: {}
    };
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="about-us">
          <ComponentBlocks blocks={blocks} />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  aboutUs: state.aboutUs
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  },
  aboutUsLoaded: (aboutUs) => {
    dispatch(aboutUsLoaded(aboutUs));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageAboutUs);