import React from 'react';
import {sysSatisfactionFeedback} from '../../../state/actions';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';

class ComponentSatisfaction extends React.Component {
  render() {
    const {sys, sysSatisfactionFeedback} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    let string = generateLanguageString(languages, sys.language.name);
    if(sys.satisfaction.value) {
      return (
        <div className={`${style.satisfaction} ${style[componentTheme]}`}>
          <div className={style.note}>
            {string.satisfaction.thanksNote}
          </div>
        </div>
      );
    } else {
      return (
        <div className={`${style.satisfaction} ${style[componentTheme]}`}>
          <div className={style.items}>
            <div className={style.item} onClick={ev => {sysSatisfactionFeedback('happy')}}>
              <div className={`${style.icon} ${style[`type--happy`]}`}>
              </div>
            </div>
            <div className={style.item} onClick={ev => {sysSatisfactionFeedback('normal')}}>
              <div className={`${style.icon} ${style[`type--normal`]}`}>
              </div>
            </div>
            <div className={style.item} onClick={ev => {sysSatisfactionFeedback('sad')}}>
              <div className={`${style.icon} ${style[`type--sad`]}`}>
              </div>
            </div>
          </div>
          <div className={style.title}>
            {string.satisfaction.title}
          </div>
        </div>
      );
    }
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

const mapDispatchToProps = dispatch => ({
  sysSatisfactionFeedback: (value) => {
    dispatch(sysSatisfactionFeedback(value));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ComponentSatisfaction);