import React from 'react';
import {pageLoading} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentBlocks from '../../components/blocks';
import ComponentContactUs from '../../components/contact-us';
import ComponentSocialLinks from '../../components/social-links';
import ComponentReport from '../../components/report';
import ComponentSuggestionsNComplaints from '../../components/suggestions-n-complaints';

class PageContactUs extends React.Component {
  componentDidMount() {
    const {pageLoading} = this.props;
    pageLoading(false);
  }
  
  componentDidUpdate() {
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    const blocks = [];
    let string = generateLanguageString(languages, sys.language.name);
    blocks[blocks.length] = {
      name: string.contactUs.contactUs,
      icon: 'contact-us--block--contact-us.png',
      content: {
        type: 'components',
        components: [<ComponentContactUs key="contact-us" />]
      }
    };
    blocks[blocks.length] = {
      name: string.contactUs.socialLinks,
      icon: 'contact-us--block--social-links.png',
      content: {
        type: 'components',
        components: [<ComponentSocialLinks key="social-links" />]
      }
    };
    blocks[blocks.length] = {
      name: string.contactUs.suggestionsNComplaints,
      icon: 'contact-us--block--suggestions-n-complaints.png',
      content: {
        type: 'components',
        components: [<ComponentSuggestionsNComplaints key="suggestions-n-complaints" />]
      }
    };
    blocks[blocks.length] = {
      name: string.contactUs.report,
      icon: 'contact-us--block--report.png',
      content: {
        type: 'components',
        components: [<ComponentReport key="report" />]
      }
    };
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="contact-us">
          <ComponentBlocks blocks={blocks} />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageContactUs);