import React from 'react';
import {connect} from 'react-redux';
import {NavLink} from "react-router-dom";
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';
import ComponentIcon from '../../components/icon';

class PartSideMenu extends React.Component {
  render() {
    const {sys, type, activeStatus, activeController} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    let componentType = '';
    if(type === 'start') {
      componentType = 'type--start';
    }
    if(type === 'end') {
      componentType = 'type--end';
    }
    let componentActive = '';
    if(activeStatus) {
      componentActive = 'status--active';
    }
    let string = generateLanguageString(languages, sys.language.name);
    let items = [];
    if(type === 'start') {
      items = [
        {icon: 'services', name: string.sideMenu.services, link: '/services'},
        {icon: 'news', name: string.sideMenu.news, link: '/news'},
        {icon: 'other-services', name: string.sideMenu.otherServices, link: '/other-services'},
        {icon: 'about-us', name: string.sideMenu.aboutUs, link: '/about-us'},
        {icon: 'contact-us', name: string.sideMenu.contactUs, link: '/contact-us'}
      ]
    }
    if(type === 'end') {
      items = [
        {icon: 'profile', name: string.sideMenu.profile, link: '/profile'},
        {icon: 'settings', name: string.sideMenu.settings , link: '/settings'},
        {icon: 'determination-people', name: string.sideMenu.determinationPeople, link: '/determination-people'}
      ]
    }
    return (
      <div className={`${style['side-menu']} ${componentType && style[componentType]} ${style[componentTheme]} ${componentActive && style[componentActive]}`}>
        <div className={style.links}>
          <div className={style.items}>
            {items.map((item, itemIndex) => (
              <NavLink activeClassName={style['status--active']} className={style.item} key={itemIndex} to={item.link}>
                <ComponentIcon name={item.icon} />
                <div className={style.name}>{item.name}</div>
              </NavLink>
            ))}
          </div>
        </div>
        <div className={style.button} onClick={ev => {activeController(false)}}>
          <ComponentIcon name="close" size="small" />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(PartSideMenu);