import React from 'react';
import {pageLoading, servicesLoaded} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import dataSettigns from '../../../config/data-settings';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentBlocks from '../../components/blocks';
import servicesEn from '../../../cache/b3391a7957193b94e5ee9c98a5be1645.cache';
import servicesAr from '../../../cache/45f9e5cd485036d19218081df0964b18.cache';

class PageServices extends React.Component {
  language = {};

  getData = () => {
    const {sys, services, pageLoading, servicesLoaded} = this.props;
    this.language = sys.language;
    const promises = [];
    if(!services.categories.length) {
      promises[promises.length] = new Promise(function(resolve, reject) {
        fetch(dataSettigns.services(sys.language.name))
        .then(data => data.json())
        .then(data => {
          resolve({action: servicesLoaded, data});
        })
        .catch(reject);
      });
    }
    if (promises) {
      Promise.all(promises)
      .then(values => {
        values.forEach(value => {
          value.action(value.data);
        });
        pageLoading(false);
      })
      .catch(reason => {
        Promise.all([
          new Promise(function(resolve, reject) {
            fetch(sys.language.name === 'en' ? servicesEn : servicesAr)
            .then(data => data.json())
            .then(data => {
              resolve({action: servicesLoaded, data});
            })
          })
        ])
        .then(values => {
          values.forEach(value => {
            value.action(value.data);
          });
          pageLoading(false);
        });
      });
    } else {
      pageLoading(false);
    }
  };

  componentDidMount() {
    this.getData();
  }
  
  componentDidUpdate() {
    const {sys, pageLoading} = this.props;
    if(this.language.name !== sys.language.name) {
      pageLoading(true);
      this.getData();
    }
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys, services} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    const blocks = services ? services.categories.map((category, categoryIndex) => {
      return {
        name: category.name,
        icon: `service--block--${(categoryIndex + 1)}.png`,
        content: {
          type: 'list',
          list: category.list
        }
      };
    }) : [];
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="services">
          <ComponentBlocks blocks={blocks} />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  services: state.services
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  },
  servicesLoaded: (categories) => {
    dispatch(servicesLoaded(categories));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageServices);