import React from 'react';
import {sideMenusStartActive, sideMenusEndActive} from '../../../state/actions';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import ComponentIcon from '../../components/icon';

class PartFooter extends React.Component {
  render() {
    const {startActive, endActive, sideMenusStartActive, sideMenusEndActive} = this.props;
    return (
      <div className={style.footer}>
        <div className={`${style.icon} ${startActive ? style['theme--hide'] : ''}`} onClick={(ev) => {sideMenusStartActive(!startActive)}}>
          <ComponentIcon name="side-menu--start" />
        </div>
        <Link to="/">
          <ComponentIcon name="home" />
        </Link>
        <div className={`${style.icon} ${endActive ? style['theme--hide'] : ''}`} onClick={(ev) => {sideMenusEndActive(!endActive)}}>
          <ComponentIcon name="side-menu--end" />
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  startActive: state.sideMenus.start,
  endActive: state.sideMenus.end
});

const mapDispatchToProps = dispatch => ({
  sideMenusStartActive: (active) => {
    dispatch(sideMenusStartActive(active));
  },
  sideMenusEndActive: (active) => {
    dispatch(sideMenusEndActive(active));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PartFooter);