import {
  APPEND_SERVICES_DATA
} from '../actions/types';

const initialState = {
  categories: []
};

const services = (state = initialState, action) => {
  switch (action.type) {
    case APPEND_SERVICES_DATA:
      return {
        ...state,
        categories: action.payload.categories
      };
    default:
      return state;
  }
};

export default services;