import React from 'react';
import {pageLoading} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import dataSettigns from '../../../config/data-settings';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentBlocks from '../../components/blocks';

class PageOtherServices extends React.Component {
  componentDidMount() {
    const {pageLoading} = this.props;
    pageLoading(false);
  }
  
  componentDidUpdate() {
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    const blocks = [];
    let string = generateLanguageString(languages, sys.language.name);
    blocks[blocks.length] = {
      name: string.otherServices.training,
      icon: 'other-services--block--training.png',
      link: `/external/${encodeURIComponent(dataSettigns.training(sys.language.name))}`,
      content: {}
    };
    blocks[blocks.length] = {
      name: string.otherServices.surveys,
      icon: 'other-services--block--surveys.png',
      link: `/external/${encodeURIComponent(dataSettigns.surveys(sys.language.name))}`,
      content: {}
    };
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="other-services">
          <ComponentBlocks blocks={blocks} />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageOtherServices);