import {
  APPEND_SLIDER_DATA
} from '../actions/types';

const initialState = {
  slides: []
};

const slider = (state = initialState, action) => {
  switch (action.type) {
    case APPEND_SLIDER_DATA:
      return {
        ...state,
        slides: action.payload.slides
      };
    default:
      return state;
  }
};

export default slider;