import {
  SYS_LANGUAGE_CHANGED,
  SYS_SATISFACTION_FEEDBACK,
  SYS_THEME_CHANGED,
  APP_LOADED,
  PAGE_LOADING,
  USER_LOG,
  SIDE_MENUS_START_ACTIVE,
  SIDE_MENUS_END_ACTIVE,
  POPUP_SATISFACTION_ACTIVE,
  POPUP_LANGUAGES_ACTIVE,
  APPEND_SLIDER_DATA,
  APPEND_E_SERVICES_DATA,
  APPEND_SERVICES_DATA,
  APPEND_NEWS_DATA,
  APPEND_ABOUT_US_DATA
} from './types';

export const changeSysLanguage = (name, direction) => {
  return {
    type: SYS_LANGUAGE_CHANGED,
    payload: {
      name,
      direction
    }
  };
};

export const sysSatisfactionFeedback = (value) => {
  return {
    type: SYS_SATISFACTION_FEEDBACK,
    payload: {
      value
    }
  };
};

export const changeSysTheme = (name, blindness) => {
  return {
    type: SYS_THEME_CHANGED,
    payload: {
      name,
      blindness
    }
  };
};

export const appLoaded = () => {
  return {
    type: APP_LOADED
  };
};

export const pageLoading = (loading) => {
  return {
    type: PAGE_LOADING,
    payload: {
      loading
    }
  };
};

export const userLog = (user) => {
  return {
    type: USER_LOG,
    payload: {
      user
    }
  };
};

export const sideMenusStartActive = (active) => {
  return {
    type: SIDE_MENUS_START_ACTIVE,
    payload: {
      active
    }
  };
};

export const sideMenusEndActive = (active) => {
  return {
    type: SIDE_MENUS_END_ACTIVE,
    payload: {
      active
    }
  };
};

export const popupSatisfactionActive = (active) => {
  return {
    type: POPUP_SATISFACTION_ACTIVE,
    payload: {
      active
    }
  };
};

export const popupLanguageActive = (active) => {
  return {
    type: POPUP_LANGUAGES_ACTIVE,
    payload: {
      active
    }
  };
};

export const sliderLoaded = (slides) => {
  return {
    type: APPEND_SLIDER_DATA,
    payload: {
      slides
    }
  };
};

export const eServicesLoaded = (categories) => {
  return {
    type: APPEND_E_SERVICES_DATA,
    payload: {
      categories
    }
  };
};

export const servicesLoaded = (categories) => {
  return {
    type: APPEND_SERVICES_DATA,
    payload: {
      categories
    }
  };
};

export const newsLoaded = (news) => {
  return {
    type: APPEND_NEWS_DATA,
    payload: {
      news
    }
  };
};

export const aboutUsLoaded = (aboutUs) => {
  return {
    type: APPEND_ABOUT_US_DATA,
    payload: {
      aboutUs
    }
  };
};