import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';
import ReactHtmlParser from 'react-html-parser';

class ComponentHTML extends React.Component {
  render() {
    const {sys, html} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.html} ${style[componentDirection]} ${style[componentTheme]}`}>
        {ReactHtmlParser(html)}
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentHTML);