import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';
import dataSettigns from '../../../config/data-settings';

class ComponentSuggestionsNComplaints extends React.Component {
  render() {
    const {sys} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    let string = generateLanguageString(languages, sys.language.name);
    return (
      <div className={`${style['suggestions-n-complaints']} ${style[componentDirection]} ${style[componentTheme]}`}>
        <div className={style.items}>
          <Link className={style.item} to={`/external/${encodeURIComponent(dataSettigns.inquiries(sys.language.name))}`}>
            <div className={`${style['icon-container']}`}>
              <div className={`${style.icon}`} style={{WebkitMaskImage: `url('${process.env.PUBLIC_URL}/files/images/suggestions-n-complaints--inquiries.png')`}}>
              </div>
            </div>
            <div className={`${style['name-container']}`}>
              <div className={`${style.name}`}>
                {string.contactUs.suggestionsNComplaintsInquiries}
              </div>
            </div>
          </Link>
          <Link className={style.item} to={`/external/${encodeURIComponent(dataSettigns.suggestions(sys.language.name))}`}>
            <div className={`${style['icon-container']}`}>
              <div className={`${style.icon}`} style={{WebkitMaskImage: `url('${process.env.PUBLIC_URL}/files/images/suggestions-n-complaints--suggestions.png')`}}>
              </div>
            </div>
            <div className={`${style['name-container']}`}>
              <div className={`${style.name}`}>
                {string.contactUs.suggestionsNComplaintsSuggestions}
              </div>
            </div>
          </Link>
          <Link className={style.item} to={`/external/${encodeURIComponent(dataSettigns.complaints(sys.language.name))}`}>
            <div className={`${style['icon-container']}`}>
              <div className={`${style.icon}`} style={{WebkitMaskImage: `url('${process.env.PUBLIC_URL}/files/images/suggestions-n-complaints--complaints.png')`}}>
              </div>
            </div>
            <div className={`${style['name-container']}`}>
              <div className={`${style.name}`}>
                {string.contactUs.suggestionsNComplaintsComplaints}
              </div>
            </div>
          </Link>
        </div>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentSuggestionsNComplaints);