import React from 'react';
import {appLoaded} from '../../state/actions';
import {connect} from 'react-redux';
import {Switch, Route} from "react-router-dom";
import style from './style.module.css';
import {generateComponentDirection} from '../../helpers/css-class-generator';
import PageHome from '../pages/home';
import PageServices from '../pages/services';
import PageNews from '../pages/news';
import PageOtherServices from '../pages/other-services';
import PageAboutUs from '../pages/about-us';
import PageContactUs from '../pages/contact-us';
import PageProfile from '../pages/profile';
import PageSettings from '../pages/settings';
import PageDeterminationPeople from '../pages/determination-people';
import PageEServices from '../pages/e-services';
import PageExternal from '../pages/external';
import ComponentIntro from '../components/intro/';

class ComponentApp extends React.Component {
  checkLoading = () => {
    const {app, page, appLoaded} = this.props;
    if(!app.loaded && !page.loading) {
      appLoaded();
    }
  };

  componentDidMount() {
    this.checkLoading();
  }
  
  componentDidUpdate() {
    this.checkLoading();
  }
  
  componentWillUnmount() {
  }

  render() {
    const {sys} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    return (
      <div className={`${style.app} ${style[componentDirection]}`}>
        <Switch>
          <Route exact path="/">
            <PageHome />
          </Route>
          <Route path="/services">
            <PageServices />
          </Route>
          <Route path="/news">
            <PageNews />
          </Route>
          <Route path="/other-services">
            <PageOtherServices />
          </Route>
          <Route path="/about-us">
            <PageAboutUs />
          </Route>
          <Route path="/contact-us">
            <PageContactUs />
          </Route>
          <Route path="/profile">
            <PageProfile />
          </Route>
          <Route path="/settings">
            <PageSettings />
          </Route>
          <Route path="/determination-people">
            <PageDeterminationPeople />
          </Route>
          <Route path="/e-services">
            <PageEServices />
          </Route>
          <Route path="/external/:link" component={PageExternal} />
        </Switch>
        <ComponentIntro />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  app: state.app,
  page: state.page
});

const mapDispatchToProps = dispatch => ({
  appLoaded: () => {
    dispatch(appLoaded());
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ComponentApp);
