import React from 'react';
import {pageLoading, newsLoaded} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import dataSettigns from '../../../config/data-settings';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentNews from '../../components/news';
import newsEn from '../../../cache/3e958c89567478726ab3d07c02fdb5b8.cache';
import newsAr from '../../../cache/605a2560e0333623c8f51637701fe808.cache';

class PageNews extends React.Component {
  language = {};

  getData = () => {
    const {sys, news, pageLoading, newsLoaded} = this.props;
    this.language = sys.language;
    const promises = [];
    if(!news.length) {
      promises[promises.length] = new Promise(function(resolve, reject) {
        fetch(dataSettigns.news(sys.language.name))
        .then(data => data.json())
        .then(data => {
          resolve({action: newsLoaded, data});
        })
        .catch(reject);
      });
    }
    if (promises) {
      Promise.all(promises)
      .then(values => {
        values.forEach(value => {
          value.action(value.data);
        });
        pageLoading(false);
      })
      .catch(reason => {
        Promise.all([
          new Promise(function(resolve, reject) {
            fetch(sys.language.name === 'en' ? newsEn : newsAr)
            .then(data => data.json())
            .then(data => {
              resolve({action: newsLoaded, data});
            })
          })
        ])
        .then(values => {
          values.forEach(value => {
            value.action(value.data);
          });
          pageLoading(false);
        });
      });
    } else {
      pageLoading(false);
    }
  };

  componentDidMount() {
    this.getData();
  }
  
  componentDidUpdate() {
    const {sys, pageLoading} = this.props;
    if(this.language.name !== sys.language.name) {
      pageLoading(true);
      this.getData();
    }
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="news">
          <ComponentNews />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  news: state.news
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  },
  newsLoaded: (news) => {
    dispatch(newsLoaded(news));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageNews);