import config from '../../config';
import {
  SYS_LANGUAGE_CHANGED,
  SYS_SATISFACTION_FEEDBACK,
  SYS_THEME_CHANGED
} from '../actions/types';

const initialState = {
  language: {
    name: config.language.name,
    direction: config.language.direction
  },
  satisfaction: {
    value: ''
  },
  theme: {
    name: config.theme.name,
    blindness: config.theme.blindness
  }
};

const sys = (state = initialState, action) => {
  switch (action.type) {
    case SYS_LANGUAGE_CHANGED:
      return {
        ...state,
        language: {
          ...state.language,
          name: action.payload.name,
          direction: action.payload.direction
        }
      };
    case SYS_SATISFACTION_FEEDBACK:
      return {
        ...state,
        satisfaction: {
          ...state.satisfaction,
          value: action.payload.value
        }
      };
    case SYS_THEME_CHANGED:
      return {
        ...state,
        theme: {
          ...state.theme,
          name: action.payload.name,
          blindness: action.payload.blindness
        }
      };
    default:
      return state;
  }
};

export default sys;