import React from 'react';
import {connect} from 'react-redux';
import {Link} from "react-router-dom";
import style from './style.module.css';
import {getClosestBySelector} from '../../../helpers/selectors';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';

class ComponentBlocks extends React.Component {
  blocksElement;

  toggleActiveBlock(index) {
    let hasActive = this.blocksElement.querySelector(`.${style.block}:nth-child(${(index + 1)})`).classList.contains(`${style['status--active']}`);
    this.blocksElement.querySelectorAll(`.${style.block}`).forEach(block => {
      block.classList.remove(`${style['status--active']}`);
    });
    if(!hasActive) {
      this.blocksElement.querySelector(`.${style.block}:nth-child(${(index + 1)})`).classList.add(`${style['status--active']}`);
    }
  }

  handleBlockClick = ev => {
    this.toggleActiveBlock([...this.blocksElement.querySelectorAll(`.${style.block}`)].indexOf(getClosestBySelector(ev.target, `.${style.block}`)));
  }

  componentConstructor() {
    this.blocksElement = document.querySelector(`.${style.blocks}`);
  }

  componentRefresh() {
    const {blocks} = this.props;
    this.blocksElement.querySelectorAll(`.${style.block}`).forEach(block => {
      block.removeEventListener('click', this.handleBlockClick);
    });
    this.blocksElement.querySelectorAll(`.${style.block}`).forEach(block => {
      block.addEventListener('click', this.handleBlockClick);
    });
    if(blocks.length) {
      this.toggleActiveBlock(0);
    }
  }

  componentDestructors() {
  }

  componentDidMount() {
    this.componentConstructor();
    this.componentRefresh();
  }
  
  componentDidUpdate() {
    this.componentRefresh();
  }
  
  componentWillUnmount() {
    this.componentDestructors();
  }
  
  render() {
    const {sys, blocks} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.blocks} ${style[componentDirection]} ${style[componentTheme]}`}>
        {blocks.map((block, blockIndex) => (
          block.hasOwnProperty('link') ? (
            <Link key={blockIndex} className={`${style.block}`} to={block.link}>
              <div className={`${style.header}`}>
                <div className={`${style['icon-container']}`}>
                  <div className={`${style.icon}`} style={{WebkitMaskImage: `url('${process.env.PUBLIC_URL}/files/images/${block.icon}')`}}>
                  </div>
                </div>
                <div className={`${style['name-container']}`}>
                  <div className={`${style.name}`}>
                    {block.name}
                  </div>
                </div>
              </div>
            </Link>
          ) : (
            <div key={blockIndex} className={`${style.block} ${Object.keys(block.content).length && style['has-content']}`}>
              <div className={`${style.header}`}>
                <div className={`${style['icon-container']}`}>
                  <div className={`${style.icon}`} style={{WebkitMaskImage: `url('${process.env.PUBLIC_URL}/files/images/${block.icon}')`}}>
                  </div>
                </div>
                <div className={`${style['name-container']}`}>
                  <div className={`${style.name}`}>
                    {block.name}
                  </div>
                </div>
              </div>
              {Object.keys(block.content).length ? (
              <div className={`${style.main}`}>
                {(() => {
                  switch(block.content.type) {
                    case 'list':
                      return (
                        <div className={`${style.list}`}>
                          <ul>
                            {block.content.list.map((item, itemIndex) => (
                              <li key={itemIndex}><Link to={`/external/${encodeURIComponent(item.link)}`}>{item.name}</Link></li>
                            ))}
                          </ul>
                        </div>
                      )
                    case 'components':
                      return (
                        <div className={`${style.component}`}>
                          {block.content.components}
                        </div>
                      )
                    default:
                      return '';
                  }
                })()}
              </div>
              ) : ''}
            </div>
          )
        ))}
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentBlocks);