import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';
import {generateLanguageString} from '../../../helpers/language-string-generator';

class ComponentContactUs extends React.Component {
  render() {
    const {sys} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    let string = generateLanguageString(languages, sys.language.name);
    return (
      <div className={`${style['contact-us']} ${style[componentDirection]} ${style[componentTheme]}`}>
        <ul>
          <li><span className={style.label}>{string.contactUs.contactUsAddressLabel}</span> : <span className={style.content}>{string.contactUs.contactUsAddressContent}</span></li>
          <li><span className={style.label}>{string.contactUs.contactUsMailBoxLabel}</span> : <span className={style.content}>{string.contactUs.contactUsMailBoxContent}</span></li>
          <li><span className={style.label}>{string.contactUs.contactUsPhoneLabel}</span> : <span className={style.content}>{string.contactUs.contactUsPhoneContent}</span></li>
          <li><span className={style.label}>{string.contactUs.contactUsFaxLabel}</span> : <span className={style.content}>{string.contactUs.contactUsFaxContent}</span></li>
          <li><span className={style.label}>{string.contactUs.contactUsCallCenterLabel}</span> : <span className={style.content}>{string.contactUs.contactUsCallCenterContent}</span></li>
        </ul>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentContactUs);