import {
  PAGE_LOADING
} from '../actions/types';

const initialState = {
  loading: true
};

const page = (state = initialState, action) => {
  switch (action.type) {
    case PAGE_LOADING:
      return {
        ...state,
        loading: action.payload.loading
      };
    default:
      return state;
  }
};

export default page;