import React from 'react';
import {changeSysLanguage, sliderLoaded, eServicesLoaded, servicesLoaded, newsLoaded, aboutUsLoaded} from '../../../state/actions';
import {connect} from 'react-redux';
import { withRouter } from "react-router";
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import languages from '../../../languages';

class ComponentLanguages extends React.Component {
  chagneLangauge = (language) => {
    const {sys, changeSysLanguage, sliderLoaded, eServicesLoaded, servicesLoaded, newsLoaded, aboutUsLoaded} = this.props;
    if(sys.language.name !== language.name) {
      changeSysLanguage(language.name, language.direction);
      sliderLoaded([]);
      eServicesLoaded([]);
      servicesLoaded([]);
      newsLoaded([]);
      aboutUsLoaded({sections: [], map: {}});
    }
  }
  
  render() {
    const {sys} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.languages} ${style[componentTheme]}`}>
        {(() => {
          let items = [];
          for(let language in languages) {
            items[items.length] = languages[language];
          }
          return items;
        })().map((language, languageIndex) => (
        <div key={languageIndex} className={style.language} onClick={ev => {this.chagneLangauge(language)}}>
          <div className={`${style.icon} ${style[`type--${language.name}`]}`}>
          </div>
          <div className={style.name}>
            {language.nativeName}
          </div>
        </div>
        ))}
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

const mapDispatchToProps = dispatch => ({
  changeSysLanguage: (name, direction) => {
    dispatch(changeSysLanguage(name, direction));
  },
  sliderLoaded: (slides) => {
    dispatch(sliderLoaded(slides));
  },
  eServicesLoaded: (categories) => {
    dispatch(eServicesLoaded(categories));
  },
  servicesLoaded: (categories) => {
    dispatch(servicesLoaded(categories));
  },
  newsLoaded: (news) => {
    dispatch(newsLoaded(news));
  },
  aboutUsLoaded: (aboutUs) => {
    dispatch(aboutUsLoaded(aboutUs));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(ComponentLanguages));