export const generateComponentDirection = (direction) => {
  return direction === 'ltr' ? 'direction--ltr' : 'direction--rtl';
}
export const generateComponentTheme = (theme) => {
  if(theme.blindness) {
    return 'theme--blindness';
  } else {
    switch(theme.name) {
      case 'gold':
        return 'theme--gold';
      default:
        return 'theme--green';
    }
  }
}