import React from 'react';
import {pageLoading, sliderLoaded, eServicesLoaded} from '../../../state/actions';
import {connect} from 'react-redux';
import style from '../style.module.css';
import dataSettigns from '../../../config/data-settings';
import {generateComponentTheme} from '../../../helpers/css-class-generator';
import LayoutBasic from '../../layouts/basic';
import ComponentSearch from '../../components/search';
import ComponentLogin from '../../components/login';
import ComponentSlider from '../../components/slider';
import ComponentEServicesCarousel from '../../components/e-services-carousel';
import ComponentEServices from '../../components/e-services';
import sliderEn from '../../../cache/b4526c50db8677ac388cd526a29bee0f.cache';
import sliderAr from '../../../cache/a86e6d6c8b4f2c53188c94bfac238957.cache';
import eServicesEn from '../../../cache/a7ef2d49dbd447ff57bc73a03b14f998.cache';
import eServicesAr from '../../../cache/282e4c098698d19a2e7fb583910a35de.cache';

class PageHome extends React.Component {
  language = {};

  getData = () => {
    const {sys, slider, eServices, pageLoading, sliderLoaded, eServicesLoaded} = this.props;
    this.language = sys.language;
    const promises = [];
    if(!slider.slides.length) {
      promises[promises.length] = new Promise(function(resolve, reject) {
        fetch(dataSettigns.slider(sys.language.name))
        .then(data => data.json())
        .then(data => {
          resolve({action: sliderLoaded, data});
        })
        .catch(reject);
      })
    }
    if(!eServices.categories.length) {
      promises[promises.length] = new Promise(function(resolve, reject) {
        fetch(dataSettigns.eServices(sys.language.name))
        .then(data => data.json())
        .then(data => {
          resolve({action: eServicesLoaded, data});
        })
        .catch(reject);
      });
    }
    if(promises){
      Promise.all(promises)
      .then(values => {
        values.forEach(value => {
          value.action(value.data);
        })
        pageLoading(false);
      })
      .catch(reason => {
        Promise.all([
          new Promise(function(resolve, reject) {
            fetch(sys.language.name === 'en' ? sliderEn : sliderAr)
            .then(data => data.json())
            .then(data => {
              resolve({action: sliderLoaded, data});
            })
          }),
          new Promise(function(resolve, reject) {
            fetch(sys.language.name === 'en' ? eServicesEn : eServicesAr)
            .then(data => data.json())
            .then(data => {
              resolve({action: eServicesLoaded, data});
            })
          })
        ])
        .then(values => {
          values.forEach(value => {
            value.action(value.data);
          });
          pageLoading(false);
        });
      });
    } else {
      pageLoading(false);
    }
  };

  componentDidMount() {
    this.getData();
  }
  
  componentDidUpdate() {
    const {sys, pageLoading} = this.props;
    if(this.language.name !== sys.language.name) {
      pageLoading(true);
      this.getData();
    }
  }
  
  componentWillUnmount() {
    const {pageLoading} = this.props;
    pageLoading(true);
  }

  render() {
    const {sys, user} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style.page} ${style[componentTheme]}`}>
        <LayoutBasic pageName="home">
          <ComponentSearch />
          {user.loggedIn ? (
            <div>
              notifications
            </div>
          ) : (
            <ComponentLogin size="small" />
          )}
          <ComponentSlider />
          <ComponentEServicesCarousel />
          <ComponentEServices size="brief" />
        </LayoutBasic>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys,
  user: state.user,
  slider: state.slider,
  eServices: state.eServices
});

const mapDispatchToProps = dispatch => ({
  pageLoading: (loading) => {
    dispatch(pageLoading(loading));
  },
  sliderLoaded: (slides) => {
    dispatch(sliderLoaded(slides));
  },
  eServicesLoaded: (categories) => {
    dispatch(eServicesLoaded(categories));
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(PageHome);