import {
  POPUP_SATISFACTION_ACTIVE,
  POPUP_LANGUAGES_ACTIVE
} from '../actions/types';

const initialState = {
  satisfaction: false,
  languages: false
};

const popups = (state = initialState, action) => {
  switch (action.type) {
    case POPUP_SATISFACTION_ACTIVE:
      return {
        ...state,
        satisfaction: action.payload.active
      };
    case POPUP_LANGUAGES_ACTIVE:
      return {
        ...state,
        languages: action.payload.active
      };
    default:
      return state;
  }
};

export default popups;