import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentTheme} from '../../../helpers/css-class-generator';

class ComponentIcon extends React.Component {
  render() {
    const {sys, name, size} = this.props;
    let componentTheme = generateComponentTheme(sys.theme);
    let componentSize = '';
    if(size === 'small') {
      componentSize = 'size--small';
    }
    let componentType = '';
    switch(name) {
      case 'close':
        componentType = 'type--close';
        break;
      case 'home':
        componentType = 'type--home';
        break;
      case 'languages':
        componentType = 'type--languages';
        break;
      case 'satisfaction':
        componentType = 'type--satisfaction';
        break;
      case 'side-menu--start':
        componentType = 'type--side-menu--start';
        break;
      case 'side-menu--end':
        componentType = 'type--side-menu--end';
        break;
      case 'profile':
        componentType = 'type--profile';
        break;
      case 'settings':
        componentType = 'type--settings';
        break;
      case 'determination-people':
        componentType = 'type--determination-people';
        break;
      case 'services':
        componentType = 'type--services';
        break;
      case 'news':
        componentType = 'type--news';
        break;
      case 'other-services':
        componentType = 'type--other-services';
        break;
      case 'about-us':
        componentType = 'type--about-us';
        break;
      case 'contact-us':
        componentType = 'type--contact-us';
        break;
      default:
        componentType = '';
    }
    return (
      <div className={`${style.icon} ${style[componentTheme]} ${componentSize && style[componentSize]} ${componentType && style[componentType]}`}>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentIcon);