import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import {generateComponentDirection, generateComponentTheme} from '../../../helpers/css-class-generator';

class ComponentSocialLInks extends React.Component {
  render() {
    const {sys} = this.props;
    let componentDirection = generateComponentDirection(sys.language.direction);
    let componentTheme = generateComponentTheme(sys.theme);
    return (
      <div className={`${style['social-links']} ${style[componentDirection]} ${style[componentTheme]}`}>
        <ul>
          <li><a href="http://instagram.com/SharjahEconomic" target="_blank" rel="noopener noreferrer"><span className={`${style.icon} ${style['type-instagram']}`}><i className="fab fa-instagram"></i></span></a></li>
          <li><a href="http://twitter.com/sharjaheconomic" target="_blank" rel="noopener noreferrer"><span className={`${style.icon} ${style['type-twitter']}`}><i className="fab fa-twitter"></i></span></a></li>
          <li><a href="http://www.youtube.com/user/SharjahEconomic?feature=mhum" target="_blank" rel="noopener noreferrer"><span className={`${style.icon} ${style['type-youtube']}`}><i className="fab fa-youtube"></i></span></a></li>
          <li><a href="http://www.facebook.com/pages/SHJEDD" target="_blank" rel="noopener noreferrer"><span className={`${style.icon} ${style['type-facebook']}`}><i className="fab fa-facebook-f"></i></span></a></li>
        </ul>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentSocialLInks);