import {
  APPEND_ABOUT_US_DATA
} from '../actions/types';

const initialState = {
  sections: [],
  map: {}
};

const aboutUs = (state = initialState, action) => {
  switch (action.type) {
    case APPEND_ABOUT_US_DATA:
      return {
        ...state,
        sections: action.payload.aboutUs.sections,
        map: action.payload.aboutUs.map
      };
    default:
      return state;
  }
};

export default aboutUs;