import React from 'react';
import {connect} from 'react-redux';
import style from './style.module.css';
import dataSettigns from '../../../config/data-settings';

class ComponentExternal extends React.Component {
  render() {
    const {sys} = this.props;
    let link = decodeURIComponent(this.props.link);
    if(!(link.startsWith("https://") || link.startsWith("https://"))) {
      link = `${dataSettigns.linkPrefix(sys.language.name)}${link}`;
    }
    return (
      <div className={`${style.external}`}>
        <iframe src={link} title="external"></iframe>
      </div>
    )
  }
}

const mapStateToProps = state => ({
  sys: state.sys
});

export default connect(
  mapStateToProps
)(ComponentExternal);