import {
  USER_LOG
} from '../actions/types';

const initialState = {
  loggedIn: false,
  information: {
    name: ''
  }
};

const user = (state = initialState, action) => {
  switch (action.type) {
    case USER_LOG:
      return {
        ...state,
        loggedIn: action.payload.user.loggedIn,
        information: {
          ...action.payload.user.information,
          name: action.payload.user.information.name
        }
      };
    default:
      return state;
  }
};

export default user;